.App{
  background-image: linear-gradient(to right ,white,#edfefe,white,#edfefe,white);
}

.Navbar_box{
  background-image: linear-gradient(to right,rgb(249, 227, 227),white,rgb(243, 218, 218));
  height: 60px;
  position: fixed;
  width: 100%;
  z-index: 200;
  display: inline;
}
.navbar_line{
  height: 5px;
  background-color: red;
  width: 0;
  transition: 1ms;
}
.Link_btns{
  cursor: pointer;
  margin-bottom: 10px;
  display: inline-block;
}
.Link_btns::after{
  content: '';
  display: block;
  width: 0;
  height: 2px;
  transition:3s;
  transition: width .3s;
  background: #000;
}

.Link_btns:hover::after{
  width: 100%;
}

.HeaderArea{
  display: flex;
  gap: 80px;
  margin-top: 0px;
  padding: 20px;
  padding-top: 90px;
  background-image: linear-gradient(to right 
  ,white,#edfefe,white,#edfefe,white);
}
.icons{
  font-size: 60px;
}
.photo_image{
  border: 2px solid black;
  border-radius: 50%;
  height: 300px;
  margin-top: 50px;
  /* margin-left: 10px; */
}
.About_image{
  height: 300px;
  margin-top: 50px;
  margin-left: 90px;
  padding-top: 20px;
}
.About_box{
  width: 70%;
  margin: auto;
  padding: 20px;
  margin-top: 0px;
  font-size: 20px;
}
.Hello_Style{
  background-color: blue;
  width: 70px;
  padding: 5px;
  color: white;
  border-top-left-radius: 15px;
  border-bottom-right-radius: 15px;
  border-top-right-radius: 15px;
}
.About_style{
  background-image: linear-gradient(to right ,white,#edfefe,white,#edfefe,white);
  font-family: sans-serif;
  line-height: 5px;
  line-height: 34px;
}

.Skills_btn{
  background-color: white;
  padding: 9px;
  font-size: 13px;
  border: 1px solid black;
  border-radius: 10px;
}
.Resume_btn{
  padding: 10px;
  border-radius: 20px;
  margin-top: 15px;
  color:white;
  background-color: blue;
  border:2px solid blue;
  cursor: pointer;
  font-size:15px
}
.Resume_btn:hover{
  transform: scale(1.1)
}

.Project_box{
  width: 70%;
  background-color: white;
  border: 2px solid aqua;
  margin: 20px auto 20px auto;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}
.techStackBox{
  border: 2px solid aqua;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  /* padding: 5px; */
  font-size: 40px;
  margin: 0px 5px 10px 0px;
  width:70%;
}
.project_desc_box{
  margin-top: -250px;
  width: 60%;
  margin-left: auto;
}
.descBox{
  /* border: 2px solid black; */
  padding: 20px;
  height: auto;
  /* width: 30%; */
  font-size: 20px;
  text-align: left;
  margin-top: -20px;
}
.project_image{
  width: 300px;
  height: 200px;
}
.project_image:hover{
  transform: scale(1.1);
}
.skills_box{
  display: grid;
  grid-template-columns: repeat(3,2fr);
  background-image: linear-gradient(to right ,white,#edfefe,white,#edfefe,white);
  gap: 20px;
  /* border: 2px solid red; */
  width: 75%;
  margin: auto;
  margin-top: 50px;
}
.skills_box div{
  /* border: 2px solid blue; */
  height: 160px;
  width: 100%;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  /* box-shadow: rgb(0 0 0 / 5%) 0px 1px 2px, rgb(224 17 17 / 8%) 0px 2px 4px, rgb(235 16 16 / 83%) 0px 4px 8px, rgb(234 27 25 / 70%) 0px 8px 16px, rgb(242 15 15 / 0%) 0px 16px 32px, rgb(255 6 6 / -45%) 0px 32px 64px; */
  text-align: center;
  padding-top: 15px;
  background-color: white;
  border-radius: 10px;
